header {
  z-index: 999;
  width: 92%;
  left: 4%;
  padding: 20px 0;
  position: absolute;
  font-size: 15px;
  overflow: hidden;
}

header nav {
  display: flex;
  padding: 15px;
}

header nav .middle{
  flex-grow: 1;

}

header nav ul {
  display: flex;
  list-style: none;
  color: var(--mainColor);
  justify-content: center;
}

header nav ul li a{
  display: block;
  margin: 0 10px;
  padding: 0 10px;
  color: var(--mainColor);
  text-decoration: none;
  text-align: right;
  bottom: 0;
  line-height: 40px;
}

header  nav ul.right a{
  padding: 0 4px;
}

.navLogo {
  /* width: 150px; */
  height: 40px;
}

.selected{
  border-bottom: 2.5px solid var(--mainColor);
  font-weight: 700;
}


@media (max-width: 600px){
header{
  width: 100%;
  left: 0;
}

  header nav {
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0;
  }
}

@media (max-width: 450px){
  header nav a {
    /* text-align: center; */
  }
  header nav ul.middle {
    flex-direction: column;
  }
  header nav ul.right {
justify-content: flex-end;
  }
}