.newsletter{
height: 70px;
line-height: 70px;
width: 100%;
text-align: center;
background-color: var(--mainColor);
display: flex;
justify-content: center;
flex-grow: 1;

position: sticky;
bottom: 0;
}

.newsletter .newsLeft {
  font-size: 18px;
 padding-right: 20px;
 text-align: right;
 color: var(--secondColor);

}

.newsletter .newsRight{
  text-align: left;
  position: relative;
}

.newsletter .newsRight .sendingMessage {
  position: absolute;
  line-height: 20px;
  top: 55px;
  font-size: 18px;
  font-weight: 700;
}

.sendingMessage.error {
  color: red;
}

.sendingMessage.sent {
  color: green;
}

.newsletter input{
  background-color: var(--mainColor);
  border: 1.5px solid var(--secondColor);
  border-radius: 5px;
  width: 250px;
  height: 35px;
  color: lightgrey;
  padding-left: 10px;
}

.newsletter input.entered{
  border: 4px solid red;
  outline: none;
}

.newsletter input:hover{
  border: 4px solid var(--secondColor);
}
.newsletter input:focus {
  border: 3px solid var(--secondColor);
  outline: none;
}

.newsletter button{
margin-left: 10px;
width: 100px;
height: 35px;
border: none;
border-radius: 5px;
background-color: var(--secondColor);
outline: none;
}

.newsletter button:hover{
  font-weight: 700;
  box-shadow: 0 0 3px var(--secondColor);
}


.copywright{
  position: absolute;
  line-height: 70px;
  width: 250px;
  right: 0;
  text-align: center;
  color: white;
}


@media (max-width: 600px){

  .newsletter .newsLeft{
    width: 100%;
    text-align: center;
    padding: 10px 0;
  }

  .newsletter{
    height: 100%;
    flex-direction: column;
    font-size: 16px;
    line-height: 20px;
    margin: 0 auto;
  }
  .copywright{
    width: 100%;
    position: static;
    line-height: 22px;
    padding: 10px 0;
  }

  .newsletter form {
    margin: 0 auto;
    text-align: center;
  }
  .newsletter input{
    width: 90%;
    margin-bottom: 10px;
  }
  }
  
